import { graphql, useStaticQuery } from "gatsby"

const query = graphql`
query ConfiguratoinQuery {
    allPrismicConfiguration {
      nodes {
        data {
          values_group {
            key {
              text
            }
            value {
              text
            }
          }
        }
        lang
      }
    }
  }
  
`

const dataResolver = (response, lang) => {
  const { allPrismicConfiguration } = response
  return allPrismicConfiguration.nodes.filter(node => node.lang === lang)[0].data
}

const useData = lang => {
  const response = useStaticQuery(query)
  return dataResolver(response, lang)
}

export default useData
