import React, { useState } from "react"
import { connect, useSelector } from "react-redux"

import { Link } from "gatsby"
import { Form, Button } from "react-bootstrap"
import { IMAGES } from "../../constants"
import { SUCCESS_MESSAGES, config } from "../../constants"
import { sendEmail } from "../../actions"
import { ConfigurationQuery } from "../../prismic/staticQueries"

const Subscription = () => {
  const language = useSelector(state => state.language)
  const [email, setEmail] = useState("")


  const [message, setMessage] = useState(null)

  const handleSubmit = async e => {
    setMessage(null)
    e.preventDefault()
    try {
      await sendEmail("subscribe", { email })
      setMessage(SUCCESS_MESSAGES.SUBSCRIBE[language])
    } catch (error) {
      console.error(error)
    }
  }

  const { values_group } = ConfigurationQuery(language)

  const Enter_your_email_to_subscribe_to_our_news = values_group[0].value[0].text
  const Submit = values_group[1].value[0].text
  const Well_keep_you = values_group[2].value[0].text
  const Updated = values_group[3].value[0].text

  return (
    <>
      <section
        className="subscribe-seven mt-5"
        style={{ backgroundImage: `url(${IMAGES.HOME_PAGE_BG})` }}
      >
        <div className="container">
          <div className="row">
            <div className="col-lg-8" style={{ margin: "auto" }}>
              <div className="text-center">
                <h2 style={{ color: "#1371b8", fontWeight: 'normal' }}>
                  {Well_keep_you}
                  <span style={{ color: "#1371b8", fontWeight: 'bold' }}> {Updated}</span>
                </h2>
                <Form
                  onSubmit={handleSubmit}
                  className="SubscriptionPanel p-left-50 p-right-50"
                >
                  <div>
                    <Form.Control
                      className="form-control"
                      type="email"
                      className="form-control"
                      onChange={e => setEmail(e.target.value)}
                      placeholder={Enter_your_email_to_subscribe_to_our_news}
                      aria-label="email"
                      required
                    />


                    <Button
                      type="submit"
                      className="btn"
                      style={{
                        backgroundColor: "white",
                        border: "unset",
                        right: "5rem",
                      }}
                    >
                      {Submit}
                    </Button>
                    {/* {language === "ar-ae" && (
                      <Button
                        type="submit"
                        className="btn"
                        style={{
                          backgroundColor: "white",
                          border: "unset",
                          left: "5rem",
                        }}
                      >
                        {Submit}
                      </Button>
                    )} */}

                  </div>
                </Form>
              </div>
            </div>
          </div>

          <div style={{ clear: "both" }}></div>
          <div className="col-sm-12 text-center">
            {message ? (
              <div
                className="alert alert-success"
                style={{ width: "50%", margin: "auto", top: "8rem" }}
              >
                {message}
              </div>
            ) : (
                ""
              )}
          </div>
        </div>
      </section>
    </>
  )
}

const mapStateToProps = state => {
  return {
    language: state.language,
  }
}

export default connect(mapStateToProps)(Subscription)
