import React from "react"
import { connect, useSelector } from "react-redux"
import { Link } from "gatsby"

import { HeaderSectionTopQuery, ConfigurationQuery } from "../../prismic/staticQueries"
import { Container, Row, Col } from "react-bootstrap"
import { setWebLanguage } from "../../reducers"
import LanguagesDropdown from "./languages-dropdown"
import { registerDemoUrl, registerLiveUrl } from '../../constants'


const TopBar = () => {
  const language = useSelector(state => state.language)

  const {
    academy_icon,
    academy_label,
    // arabic_flag,
    cyse_link,
    // demo_account_icon,
    // live_account_icon,

    demo_account_label,
    live_account_label,

    // english_flag,
    heading_label,

    contact_link,
    contact_label,

    portal_icon,
    portal_label,
    academy_link,
    portal_link,
    live_account_link,
    demo_account_link,
  } = HeaderSectionTopQuery(language)
  const { values_group } = ConfigurationQuery(language)
  const WebTrader = values_group[4].value[0].text


  return (
    <>
      {/* <div className="top_bar d-flex align-items-center text-blue-dark">
      <Container>
        <Row>
          <Col md={12}> */}
      <div className="d-flex topbar_content justify-content-between">

        <div className="aside-top">
          <ul>
            <li>
              <Link className="smallIcon grey" to={portal_link[0].text} >
                {/* <img alt="ddd" src={portal_icon.url} />  */}
                {portal_label[0].text}
              </Link>
            </li>
            <li>
              <Link className="smallIcon grey" to={academy_link[0].text}>
                {/* <img alt="ddd" src={academy_icon.url} />  */}
                {academy_label[0].text}
              </Link>
            </li>
            <li>
              <LanguagesDropdown />
            </li>
            {/* <li>
                    <a
                      target="_blank"
                      rel="noreferrer"
                      className="smallButton"
                      href={demo_account_link[0].text}
                    >
                      <button type="button" className="btn btn-small account-btn demo">
                        {demo_account_label[0].text}
                      </button>
                    </a>
                  </li> */}
            {/* <li >
                    <a
                      target="_blank"
                      rel="noreferrer"
                      className="smallButton cta-button"
                      href={live_account_link[0].text}
                    >
                      <button type="button" className="btn btn-small account-btn live">
                      {live_account_label[0].text}
                      </button>
                      
                    </a>
                  </li> */}
          </ul>
        </div>


        <div className="aside-bottom">
          <ul>
            <li >
              <Link to={'/Trading/Webtrader'}
                className={`smallIcon grey 
                ${language === 'en-us' ? 'mr-3 dropdown-item subMenu'
                    : language === 'ar-ae' ? 'ml-3' :
                      language === 'fa-ir' ? "ml-3" : 'mr-3'
                  }`}
                style={{ display: 'initial', color: '#055563', fontSize: '1rem', background: 'none', fontWeight: 300 }}
              >
                {WebTrader}
              </Link>
              <Link to={registerLiveUrl}
                className="smallButton cta-button"
              >
                {live_account_label[0].text}
              </Link>

            </li>
          </ul>
        </div>

      </div>
      {/*   </Col>
        </Row>
      </Container>
        </div> */}
    </>
  )
}

const mapStateToProps = state => ({ language: state.language })

export default connect(mapStateToProps, { setWebLanguage })(TopBar)
