import React from "react"
import propTypes from "prop-types"
import { connect } from "react-redux"
import { Dropdown, Card } from "react-bootstrap"
import { config } from '../../constants';


import i18n from '../../helpers/i18n';
import { HeaderSectionTopQuery } from "../../prismic/staticQueries"
import { setWebLanguage } from "../../reducers"

const LanguagesDropdown = ({ language = config.arabicLanguage, setWebLanguage }) => {
  const { arabic_flag, english_flag } = HeaderSectionTopQuery(language)

  // const languages = {
  //   [config.arabicLanguage]: {
  //     title: "",
  //     flag: arabic_flag.url,
  //   },
  //   [config.englishLanguage]: {
  //     title: "English",
  //     flag: english_flag.url,
  //   },
  // }
  var isRtl
  language === "ar-ae" && (isRtl = true)
  language === "so-kr" && (isRtl = true)
  language === "fa-ir" && (isRtl = true)

  const languages = []
  config.languages.forEach(obj => {
    languages.push({
      title: obj.title,
      titleCode: obj.titleCode,
      flag: obj.flag,
      key: obj.key
    })
  });

  var currentTitle = languages.filter(obj => obj.key === language) &&
    languages.filter(obj => obj.key === language)[0] &&
    languages.filter(obj => obj.key === language)[0].title

  var currentFlag = config.languages.find(f => f.title === currentTitle)

  const anotherLanguage = language === config.englishLanguage ? config.arabicLanguage : config.englishLanguage

  const switchLanugage = (lang) => {
    let locale;
    switch (lang.key) {
      case 'ar-ae':
        locale = 'ar';
        break;
      case 'ja-jp':
        locale = 'jp';
        break;
      case 'zh-cn':
        locale = 'ch';
        break;
      case 'es-es':
        locale = 'es';
        break;
      case 'so-kr':
        locale = 'so';
        break;
      case 'fa-ir':
        locale = 'fa';
        break;
      case 'ru':
        locale = 'ru';
        break;
      case 'en-us':
      default:
        locale = 'en';
    }
    i18n.changeLanguage(locale)
    // i18n.changeLanguage(anotherLanguage === 'ar-ae' ? 'ar' : 'en')
    setWebLanguage(lang.key);
  }


  return (
    <>
      <Dropdown style={{ background: "unset" }}>
        <Dropdown.Toggle
          id="language-selector"
          className="text-dark"
          style={{ background: "unset", border: "unset", padding: "unset" }}
          as={Card.Header}
        >
          <img
            className="lang_flag mx-2"
            src={currentFlag.flag}
            alt="English"
            style={{ width: "23px" }}
          />
          <span className="lang">
            {currentFlag.titleCode}
          </span>
        </Dropdown.Toggle>
        <Dropdown.Menu style={{ padding: '0px', width: 100, textAlign: "left", }}>
          {languages.map((lang, index) => <Dropdown.Item key={index} onClick={(e) => { switchLanugage(lang) }}>

            {isRtl ? <>
              {lang.titleCode}
              <img
                className="lang_flag mx-2"
                src={languages[index].flag}
                alt="English"
                style={{ width: "23px" }}
              />
            </>
              : <>
                <img
                  className="lang_flag mx-2"
                  src={languages[index].flag}
                  alt="English"
                  style={{ width: "23px" }}
                />
                {lang.titleCode}
              </>
            }

            {/* {JSON.stringify(lang)} */}
          </Dropdown.Item>)}
          {/* <Dropdown.Item onClick={switchLanugage}>
            {languages[anotherLanguage].title}
          </Dropdown.Item> */}
        </Dropdown.Menu>
      </Dropdown>
    </>
  )
}

LanguagesDropdown.prototype = {
  setWebLanguage: propTypes.func.isRequired,
}

const mapStateToProps = state => ({ language: state.language })

export default connect(mapStateToProps, { setWebLanguage })(LanguagesDropdown)
