import React, { useState } from "react"

import { useScrollPosition } from '@n8tb1t/use-scroll-position'

import { HeaderSectionQuery, HeaderSectionTopQuery } from "../../prismic/staticQueries"

import { connect, useSelector } from "react-redux"
import TopBar from "./top-bar"
import LanguagesDropdown from "./languages-dropdown"
import { Nav, Navbar, NavDropdown, Row, Col } from "react-bootstrap"
import { Link } from "gatsby"
import SEO from './../seo';
import logo2 from '../../assets/images/common/ethihadBrokerage.jpg';
const HeaderSection = () => {
  let tx = null;
  const language = useSelector(state => state.language)
  let {
    logo,
    body,
  } = HeaderSectionQuery(language);
  const {
    portal_icon,
    portal_label,
    portal_link,
    heading_label,
    cyse_link,
  } = HeaderSectionTopQuery(language)

 

  const hasSplitMenu = (items = []) => {
    for (let index = 0; index < items.length; index++) {
      const item = items[index];
      if (item.on_right === true)
        return true;
    }
    return false;
  }
  // console.log('body is ', body)
  var isRtl
  language === "ar-ae" && (isRtl = true)
  language === "so-kr" && (isRtl = true)
  language === "fa-ir" && (isRtl = true)

  const [activeItem, setActiveItem] = useState(null);
  const [isSticky, setSticky] = useState(false);

  const toggleMenu = index => {
    setActiveItem(activeItem === index ? null : index)
  };
  const enterMenu = index => {
    if (tx) clearTimeout(tx);
    setActiveItem(index)
  }
  const leaveMenu = index => {
    tx = setTimeout(index => {
      toggleMenu(index);
    }, 100);
  };

  useScrollPosition(({ prevPos, currPos }) => {
    if (currPos.y < -150 && !isSticky) {
      setSticky(true)
    } else if (currPos.y > -150 && isSticky) {
      setSticky(false)
    }
  })

  const FSCM = language === "so-kr" && true
  return (
    <>
      {/* <SEO 
              title="AccuIndex - Trade Forex, Shares, Commodities and CFDs | AccuIndex" 
              description="Accuindex provides over 200 financial instruments: currency, commodity, share CFDs  and more. Learn more about the benefits and sign up for an account today. Transparent Fee Structure."
            /> */}

      <section
        className={`header header--2 ${isSticky && "sticky"}`}
        style={{ direction: isRtl ? "rtl" : "ltr" }}
      >


        <div className="menu_area menu1">
          <div className="container">
            <Navbar collapseOnSelect expand="lg">
              {/* <Navbar.Brand href="/" className="pr-4">
                <img src={logo.url} alt="" />
              </Navbar.Brand> */}
              <Col xs={12} lg={'auto'} className="nav-col px-0 d-flex justify-content-between" dir={isRtl ? "rtl" : "ltr"}>
                <Link to={'/'} className="pr-4 navbar-brand">
                  <img src={logo2} alt="" />
                </Link>
                <div className="d-flex align-items-center">
                  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                </div>
              </Col>
              <Col className="px-0">
                <Row className="top_bar d-none d-lg-flex systemNames">
                  <Col className={`${isRtl ? "text-right" : "text-left"}`} style={{ fontSize: "0.7rem", paddingTop: "0.6rem" }}>
                    {heading_label[0].text}
                    {language !== 'ja-jp' && <span style={{ direction: isRtl ? 'rtl' : 'ltl' }}>{" "}
                      {!FSCM && <>
                        <span style={{ display: 'inline-block' }}>FSCM</span>
                        {" "}{isRtl ? "" : "&"}
                        <a
                          // className="btn-arrow btn-icon icon-left"
                          target="_blank"
                          rel="noreferrer"
                          href={cyse_link[0].text}
                        >CySEC</a>
                      </>}
                    </span>}
                    {/* <TopBar /> */}
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <Navbar.Collapse id="responsive-navbar-nav">
                      <Nav className="">

                        <div className="nav-item dropdown">
                          <ul className="mobile-only">
                            <li>
                              <a
                                className="smallIcon"
                                href={portal_link[0].text}
                                target="_blank" rel="noreferrer"
                              >
                                {/* <img alt="ddd" src={portal_icon.url} /> */}
                                {portal_label[0].text}
                              </a>
                            </li>
                          </ul>

                          <button
                            className="navbar-toggler"
                            type="button"
                            data-
                            toggle="collapse"
                            aria-expanded="false"
                            aria-label="Toggle navigation"
                          >

                            <div className="top_bar--lang align-self-center order-2 abc">

                              {/* <div className="top_bar_social">
                                <ul>
                                  <li>
                                    <a
                                      className="smallIcon"
                                      href={portal_link[0].text}
                                      target="_blank" rel="noreferrer"
                                    >
                                      <img alt="ddd" src={portal_icon.url} /> {portal_label[0].text}
                                    </a>
                                  </li>
                                </ul>
                              </div> */}

                              <LanguagesDropdown />
                            </div>
                          </button>
                        </div>

                        {body.map((menue, index) => (
                          <div key={'keys-' + index}>
                            {menue.items.length === 0 && (
                              <div className="nav-item dropdown" key={'upper-key-' + index}>
                                <Link key={'upper-key-ddf-' + index}
                                  className="nav-link homeMenue"
                                  to={menue.primary.menu_link[0].text}
                                >
                                  {" "}
                                  {menue.primary.menu_name[0].text}
                                </Link>
                              </div>
                            )}

                            {menue.items.length > 0 && (
                              <>
                                <NavDropdown
                                  key={'dropdown-key-' + index}
                                  title={menue.primary.menu_name[0].text}
                                  id="collasible-nav-dropdown"
                                  onMouseOver={() => enterMenu(index)}
                                  onMouseLeave={() => leaveMenu(index)}
                                  show={activeItem === index}
                                  style={{ zIndex: 10000 }}
                                  className={`d-none d-md-block ${isRtl
                                    ? "mainMenue menu-arrow-ar"
                                    : "mainMenue menu-arrow "
                                    }`}
                                >
                                  <ul className="" style={{ textAlign: isRtl ? 'right' : 'left', display: 'inline-block', verticalAlign: 'top' }} >
                                    {menue.items.map((sub_menue, subMenueIndex) => (
                                      <div key={subMenueIndex}>
                                        {sub_menue.on_right === false &&
                                          <div key={'sdfsdfs-' + subMenueIndex}>
                                            {
                                              (sub_menue.open_external === true && (
                                                <a
                                                  key={'inner-link-key-' + subMenueIndex}
                                                  className="dropdown-item subMenue"
                                                  target="_blank"
                                                  rel="noreferrer"
                                                  href={sub_menue.sub_menu_link[0].text}
                                                >
                                                  {" "}
                                                  {sub_menue.sub_menu_name[0].text}{" "}
                                                </a>
                                              )) ||
                                              (sub_menue.open_external === false && (
                                                <Link
                                                  key={'inner-link-key-' + subMenueIndex}
                                                  state={{ params: sub_menue.params }}
                                                  className="dropdown-item subMenue"
                                                  to={sub_menue.sub_menu_link[0].text}
                                                >
                                                  {" "}
                                                  {sub_menue.sub_menu_name[0].text}{" "}
                                                </Link>
                                              ))
                                            }
                                          </div>
                                        }


                                      </div>
                                    ))}
                                  </ul>

                                  {hasSplitMenu(menue.items) &&
                                    <ul className="" style={{ display: 'inline-block', verticalAlign: 'top', minWidth: '12rem', paddingLeft: isRtl ? '0rem' : '2rem', paddingRight: isRtl ? '2rem' : '0rem' }}>
                                      {menue.items.map((sub_menue, subMenueIndex) => (
                                        <div key={subMenueIndex}>
                                          {sub_menue.on_right === true &&
                                            <div key={'sdfsdfs-' + subMenueIndex}>
                                              {
                                                (sub_menue.open_external === true && (
                                                  <a
                                                    key={'inner-link-key-' + subMenueIndex}
                                                    className="dropdown-item subMenue"
                                                    target="_blank"
                                                    rel="noreferrer"
                                                    href={sub_menue.sub_menu_link[0].text}
                                                  >
                                                    {" "}
                                                    {sub_menue.sub_menu_name[0].text}{" "}
                                                  </a>
                                                )) ||
                                                (sub_menue.open_external === false && (
                                                  <Link
                                                    key={'inner-link-key-' + subMenueIndex}

                                                    className="dropdown-item subMenue"
                                                    to={sub_menue.sub_menu_link[0].text}
                                                  >
                                                    {" "}
                                                    {sub_menue.sub_menu_name[0].text}{" "}
                                                  </Link>
                                                ))
                                              }
                                            </div>

                                          }
                                        </div>
                                      ))}
                                    </ul>
                                  }


                                </NavDropdown>
                                <NavDropdown
                                  key={'dropdown-m-key-' + index}
                                  title={menue.primary.menu_name[0].text}
                                  id="collasible-nav-dropdown"
                                  onToggle={() => {
                                    toggleMenu(index)
                                  }}
                                  show={activeItem === index}
                                  style={{ zIndex: 10000 }}
                                  className={`d-block d-md-none ${isRtl
                                    ? "mainMenue menu-arrow-ar"
                                    : "mainMenue menu-arrow "
                                    }`}
                                >
                                  <ul className="" style={{ textAlign: isRtl ? 'right' : 'left', display: 'inline-block', verticalAlign: 'top' }} >
                                    {menue.items.map((sub_menue, subMenueIndex) => (
                                      <div key={subMenueIndex}>
                                        {sub_menue.on_right === false &&
                                          <div key={'sdfsdfs-' + subMenueIndex}>
                                            {
                                              (sub_menue.open_external === true && (
                                                <a
                                                  key={'inner-link-key-' + subMenueIndex}
                                                  className="dropdown-item subMenue"
                                                  target="_blank"
                                                  rel="noreferrer"
                                                  href={sub_menue.sub_menu_link[0].text}
                                                >
                                                  {" "}
                                                  {sub_menue.sub_menu_name[0].text}{" "}
                                                </a>
                                              )) ||
                                              (sub_menue.open_external === false && (
                                                <Link
                                                  key={'inner-link-key-' + subMenueIndex}
                                                  state={{ params: sub_menue.params }}
                                                  className="dropdown-item subMenue"
                                                  to={sub_menue.sub_menu_link[0].text}
                                                >
                                                  {" "}
                                                  {sub_menue.sub_menu_name[0].text}{" "}
                                                </Link>
                                              ))
                                            }
                                          </div>
                                        }


                                      </div>
                                    ))}
                                  </ul>

                                  {hasSplitMenu(menue.items) &&
                                    <ul className="" style={{ display: 'inline-block', verticalAlign: 'top', minWidth: '12rem', paddingLeft: isRtl ? '0rem' : '2rem', paddingRight: isRtl ? '2rem' : '0rem' }}>
                                      {menue.items.map((sub_menue, subMenueIndex) => (
                                        <div key={subMenueIndex}>
                                          {sub_menue.on_right === true &&
                                            <div key={'sdfsdfs-' + subMenueIndex}>
                                              {
                                                (sub_menue.open_external === true && (
                                                  <a
                                                    key={'inner-link-key-' + subMenueIndex}
                                                    className="dropdown-item subMenue"
                                                    target="_blank"
                                                    rel="noreferrer"
                                                    href={sub_menue.sub_menu_link[0].text}
                                                  >
                                                    {" "}
                                                    {sub_menue.sub_menu_name[0].text}{" "}
                                                  </a>
                                                )) ||
                                                (sub_menue.open_external === false && (
                                                  <Link
                                                    key={'inner-link-key-' + subMenueIndex}

                                                    className="dropdown-item subMenue"
                                                    to={sub_menue.sub_menu_link[0].text}
                                                  >
                                                    {" "}
                                                    {sub_menue.sub_menu_name[0].text}{" "}
                                                  </Link>
                                                ))
                                              }
                                            </div>

                                          }
                                        </div>
                                      ))}
                                    </ul>
                                  }


                                </NavDropdown>
                              </>
                            )}
                          </div>
                        ))}
                      </Nav>
                    </Navbar.Collapse>

                  </Col>
                </Row>
              </Col>
              <Col md={'auto'} className={`d-none d-lg-flex menu-aside align-self-start justify-content-end ${isRtl ? "text-left" : "text-right"}`}>
                <TopBar />
              </Col>
            </Navbar>
          </div>
        </div>
      </section>
    </>
  )
}

const mapStateToProps = state => {
  return {
    language: state.language,
  }
}

export default connect(mapStateToProps)(HeaderSection)
