import React, { useState } from "react"
import { connect, useSelector } from "react-redux"
import { Link } from "gatsby"
import { Container, Row, Col } from "react-bootstrap"
import { FooterSectionQuery, FooterBarQuery } from "../../prismic/staticQueries"
import { PrismicRichText } from "../shared"
// import { trans } from "../../constants"
// import PaymentGateways from "../../pages/home/payment-gateways"
import { useHomePageData } from "../../prismic/staticQueries"
import { AwardSection } from '../../components/shared';

const Footer = () => {
  const language = useSelector(state => state.language)
  const { offices_box, office_heading_label, body } = FooterSectionQuery(
    language
  )
  let {
    main_content,
    reserved_label,
    social_icons_box,
    websites_box,
  } = FooterBarQuery(language)



  social_icons_box = [{"icon_class":[{"text":"fab fa-linkedin"}],"icon_link":[{"text":"https://www.linkedin.com/company/bankaletihadjo"}]},{"icon_class":[{"text":"fab fa-facebook-f"}],"icon_link":[{"text":"https://www.facebook.com/Bank-al-Etihad-237104520366062/"}]},{"icon_class":[{"text":"fab fa-twitter"}],"icon_link":[{"text":"https://twitter.com/BankalEtihadJO"}]},{"icon_class":[{"text":"fab fa-instagram"}],"icon_link":[{"text":"https://www.instagram.com/bankaletihadjo/?hl=en"}]},{"icon_class":[{"text":"fab fa-youtube"}],"icon_link":[{"text":"https://www.youtube.com/channel/UCXIPd2PxUzggOAtCSr59o7g"}]}]

  
  const [activeOffice, setActiveOffice] = useState(0)
  var isRtl
  language === "ar-ae" && (isRtl = true)
  language === "so-kr" && (isRtl = true)
  language === "fa-ir" && (isRtl = true)
  const footerData = useHomePageData(language)
  return (
    <footer
      // className="mt-5 footer"
      style={{
        direction: isRtl ? "rtl" : "ltr",
        textAlign: isRtl ? "right" : "left",
      }}
    >
      <div className="pb-5 footer-menu">
        <Container>
          <Row>
            {body.map((item, indbody) => (
              <Col key={'ddd-' + indbody} lg={2} md={6} sm={6} className="mt-3">
                <h4 className="my-3">{item.primary.menu_label[0].text}</h4>
                <div className="footer-links">
                  {item.items.map((sub_menu, subIndex) => (
                    <div className="my-2" key={'ddd-' + subIndex} >
                      {(sub_menu.open_external === true && (
                        <a
                          target="_blank"
                          rel="noreferrer"
                          href={sub_menu.sub_menu_link[0].text}
                        >
                          {sub_menu.sub_menu_label[0].text}
                        </a>
                      )) ||
                        (sub_menu.open_external === false && (
                          <Link to={sub_menu.sub_menu_link[0].text}>
                            {sub_menu.sub_menu_label[0].text}
                          </Link>
                        ))}
                    </div>
                  ))}
                </div>
              </Col>
            ))}

            <Col lg={2} md={6} sm={6} className="mt-3">
              {/* <h4 className="my-3">{office_heading_label[0].text}</h4>
              {offices_box.map((office, index) => (
                <div className="mb-3" key={'ddd-' + index}>
                  <img
                    alt="ddd"
                    src={office.location_icon.url}
                    className="location-selector"
                    onClick={() => setActiveOffice(index)}
                    onMouseOver={() => setActiveOffice(index)}
                    onFocus={() => setActiveOffice(index)}
                  />
                </div>
              ))} */}
            </Col>
            <Col lg={4} md={6} sm={6} className="mt-3">
              {/* <div className="text-gray my-3">
                <div className="mb-2">
                  <img alt="ddd"
                    height="21px"
                    src={offices_box[activeOffice].location_icon.url}
                  />
                  <span className="mx-2">
                    {offices_box[activeOffice].location_title[0].text}
                  </span>
                </div>
                <div className="footer-office">
                  <PrismicRichText
                    data={offices_box[activeOffice].location_description}
                  />
                </div>
              </div> */}
            </Col>
          </Row>
        </Container>
      </div>

    </footer>
  )
}

const mapStateToProps = state => ({ language: state.language })

export default connect(mapStateToProps)(Footer)
