/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { useEffect } from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import {  useSelector } from "react-redux"
import { connect } from "react-redux"

// import "./../../assets/theme-1/index.scss";
import "./../../assets/theme/index.scss"

import Header from "./header"
import Footer from "./footer"
import FixIcons from "./fix-icons"
import { Helmet } from "react-helmet"

import { HeaderSectionQuery, CallBackQuery } from "../../prismic/staticQueries"
import { config } from '../../constants'
import { setWebLanguage } from "../../reducers"

const { paysafeKeys } = config;

// const RTL = React.lazy(() => import("./rtl"));
const Layout = ({ children, setWebLanguage, hideFooter=false ,hideheader=false}) => {
  const language = useSelector(state => state.language)
  if(language === 'en-gb'){
    setWebLanguage('en-us')
  }

  var isRtl
  language === "ar-ae" && (isRtl = true)
  language === "so-kr" && (isRtl = true)
  language === "fa-ir" && (isRtl = true)

  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const {
    // logo,
    // body,
    callback_icon,
    callback_title,
    callback_form_image,
    chat_icon,
    chat_title,
    user_guide_icon,
    user_guide_title,
    user_guide_link,
  } = HeaderSectionQuery(language)
  const callback_form = CallBackQuery(language)

  const callback = {
    title: callback_title,
    icon: callback_icon,
    form: callback_form,
    formImage: callback_form_image,
  }
  const chat = { title: chat_title, icon: chat_icon }
  const userGuide = {
    title: user_guide_title,
    icon: user_guide_icon,
    link: user_guide_link,
  }

  const setZendeskLang =  (lang) => {
    if(typeof window !== "undefined" && window.zE && window.zE.setLocale) {
      window.zE.setLocale(lang);
    } else {
      setTimeout(() => setZendeskLang(lang), 1000)
    }
  }

  let locale = 'en';
  useEffect(() => {
    switch (language) {
      case 'ar-ae':
        locale = 'ar';
        break;
      case 'ja-jp':
        locale = 'ja';
        break;
      case 'zh-cn':
        locale = 'zh-tw';
        break;
      case 'es-es':
        locale = 'es';
        break;
      case 'so-kr':
        locale = 'so';
        break;
      case 'fa-ir':
        locale = 'fa';
        break;
      case 'ru':
        locale = 'ru';
        break;
      case 'en-us':
      default:
        locale = 'en';
    }
    setZendeskLang(locale);
  }, [language])

  return (
    <div className={language === "so-kr" && "bodyFont"}>
      <>
        <Helmet>
        <script async src="https://www.googletagmanager.com/gtag/js?id=UA-160029705-1"></script>
        <script>
          {`window.dataLayer = window.dataLayer || [];
          function gtag(){dataLayer.push(arguments);}
          gtag('js', new Date());
          gtag('config', 'UA-160029705-1');`}
        </script>
        <script async src="https://www.googletagmanager.com/gtag/js?id=G-86H5JH6GET"></script>
        <script>
          {`window.dataLayer = window.dataLayer || [];
          function gtag(){dataLayer.push(arguments);}
          gtag('js', new Date());
          gtag('config', 'G-86H5JH6GET');`}
        </script>
          <title>{data.site.siteMetadata.title}</title>
          <link
            rel="icon"
            type="image/png"
            sizes="32x32"
            href="https://accuindex.com/Content/img/favicon.png"
          />
          {/* <html lang={language} dir={isRtl ? 'rtl' : 'ltr'}/> */}

          {/* <script src="https://v2.zopim.com/?4QNfK2zxJsGykOSioeH898I9DlkYa2Db"></script> */}
          <script type="text/javascript">
           
            {`
              window.zESettings = { 
                webWidget: { 
                  answerBot: {
                    avatar: { 
                      url: 'https://exiniti.blob.core.windows.net/public/accuindex_chat.png', 
                      name: { "*" : 'Accuindex Support' 
                      }
                    }, 
                    title: { "en-US": "Accuindex" 
                    },
                    search: { labels: ['live', 'regulation', 'platform', 'password', 'demo', 'deposit', 'withdraw', 'transfer', 'bonus', 'referral link', 'complaint', 'request', 'types', 'swap free', 'commission', 'products', 'spread', 'partnership', 'introducing broker', 'scalping', 'stop out', 'margin', 'leverage', 'additional', 'contact']
                    }
                  }
                }
              };
            `}
          </script>
          
          {/* <script id="ze-snippet" src="https://static.zdassets.com/ekr/snippet.js?key=df41e3d9-8906-4208-9a1e-f02601565dfa"> </script> */}
          {/* <script async id="convrs-webchat" src="https://webchat.conv.rs/86061d38b0bc3ff793f0c8ee19c23e6e30f10858.js"></script> */}
          <script src={paysafeKeys.sdk}></script>
          <script src="https://trade.mql5.com/trade/widget.js"></script>
          {/* <meta name="description" content="Accuindex provides over 200 financial instruments: currency, commodity, share CFDs  and more. Learn more about the benefits and sign up for an account today. Transparent Fee Structure."></meta> */}
          {/* <script>
              {`
                (function (e, x, n, t, i, w, g) {e['ExinitiRegisterWidget'] = i; e[i] = e[i] || function(){(e[i].q=e[i].q||[]).push(arguments) }; e[i].l=1*new Date();w = x.createElement(n), g = x.getElementsByTagName(n)[0];w.async = 1;w.src = t; w.id = e[i].l;g.parentNode.insertBefore(w,g);})(window, document, 'script', 'https://exiniti.blob.core.windows.net/public/rw.js', 'wx');
              `}
          </script> */}
      </Helmet>



        {!hideheader&&<Header />}

        <main
          dir={isRtl ? "rtl" : "ltr"}
          style={{ textAlign: isRtl ? "right" : "left" }}
        >
          {children}

          {/* <FixIcons callback={callback} chat={chat} userGuide={userGuide} /> */}
        </main>
        {hideFooter === false &&
          <Footer
            dir={isRtl ? "rtl" : "ltr"}
            style={{ textAlign: isRtl ? "right" : "left" }}
          />
        }
        
       
        {/* </div> */}
      </>
    </div>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default connect(null, { setWebLanguage })(Layout)
